import { Controller, Node, Block } from '@ali/4ever-cangjie';
import { message } from '@ali/we-design';
import RefBlock from '../../mo/models';
import { isInReadOnlyRefBlock, isInRefBlock } from '../../utils/logic';

export default function createDisableDrop(
  /** 无法 drop 到引用块文案 */
  cannotDropAtRefBlockTip: string,
  /** 无法 drag 引用块到某个节点文案 */
  cannotDragRefBlockToTip: string,
  /** 是否节点不允许存在于引用块 */
  disableNodeInRefBlock: (controller: Controller, node: Node) => boolean,
  /** 是否引用块无法被 drop 到某个节点 */
  disableRefBlockDropTo: (dropBlock: Block, controller: Controller) => boolean,
) {
  function containsDisabledNode(controller: Controller, dragBlock: Block) {
    return (
      disableNodeInRefBlock(controller, dragBlock) ||
      dragBlock.findDescendant((descendant) =>
        disableNodeInRefBlock(controller, descendant),
      )
    );
  }

  function containsRefBlock(dragBlock: Block) {
    return (
      RefBlock.isRefBlock(dragBlock) ||
      dragBlock.findDescendant((descendent) => RefBlock.isRefBlock(descendent))
    );
  }

  /**
   * 禁止放置
   * - 尝试拖动同步块到引用块
   * - 尝试拖动引用块到引用块
   * - 尝试拖动引用块到模板按钮
   * @param dragBlock
   * @param dropBlock
   * @returns
   */
  return function disableDrop(
    controller: Controller,
    dragBlock: Block,
    dropBlock: Block,
  ) {
    const { value } = controller;
    // Drag RefBlock to ...
    if (
      disableRefBlockDropTo(dropBlock, controller) &&
      containsRefBlock(dragBlock)
    ) {
      message.error(cannotDragRefBlockToTip);
      return true;
    }

    // Drop Something at RefBlock
    if (
      isInRefBlock(value, dropBlock) &&
      containsDisabledNode(controller, dragBlock)
    ) {
      message.error(cannotDropAtRefBlockTip);
      return true;
    }

    return isInReadOnlyRefBlock(value, dropBlock);
  };
}
